.work {
  z-index:5;
  position:relative;
  background-color: $_main_dark;
  overflow: hidden;
}

.work__container {
  padding: 23px 4.4% 54px;
}

.work__slogan {
  position: relative;
  margin: 0;
  padding: 0 0 70px;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 300;
  font-size: 55px;
  line-height: 58px;
  color: $_main_light;
  letter-spacing: -3px;

  &::before {
    position: absolute;
    content: '';
    bottom: 30px;
    right: 0px;
    width: min(120vw,750px);
    height: min(120vw,750px);
    background-image: url("data:image/svg+xml, %3Csvg fill='none' stroke='%235D9F93' xmlns='http://www.w3.org/2000/svg' id='icon-plus' viewBox='0.9 0.9 24.2 24.2'%3E%3Cg stroke-width='0.07' fill-rule='evenodd'%3E%3Cg id='Learn' transform='translate(-249.000000, -3678.000000)'%3E%3Cg id='Group-6' transform='translate(250.000000, 3679.000000)'%3E%3Cpath d='M24,24 L12.0006916,24 C5.40141779,24 0,18.5999654 0,12.0006916 C0,5.40141779 5.40141779,0 12.0006916,0 C18.5999654,0 24,5.40141779 24,12.0006916 L24,24 Z' vector-effect='non-scaling-stroke' stroke-width='2px'/%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: top left;
    background-size: cover;
  }
}

.work__text {
  margin: 0;
  padding: 0 0 28px;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: $_main_light;
  letter-spacing: -0.5px;
}

.work__title {
  margin: 0;
  padding: 0 0 7px;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 52px;
  color: $_main_light;
  letter-spacing: -1px;
}

.work__list {
  @include list-style-reset;
}

.work__item {
  padding: 20px 0;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: $_main_light;
  border-bottom: 1px solid $_font_grass;
  letter-spacing: -1px;
}

.work__item:last-child {
  border-bottom: none;
}
