@font-face {
  font-family: "Akkurat LL";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("./fonts/AkkuratLLCyr-Light.woff2") format("woff2"), url("./fonts/AkkuratLLCyr-Light.woff") format("woff");
}
@font-face {
  font-family: "Akkurat LL";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("./fonts/AkkuratLLCyr-Regular.woff2") format("woff2"), url("./fonts/AkkuratLLCyr-Regular.woff") format("woff");
}
*, *::before, *::after {
  box-sizing: inherit;
}

html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif, sans-serif;
  font-weight: normal;
}

img {
  max-width: 100%;
  width: 100%;
}

a {
  text-decoration: none;
}

.hidden {
  position: absolute;
  width: 0.1px !important;
  height: 0.1px !important;
  margin: 0 !important;
  padding: 0 !important;
  opacity: 0 !important;
  overflow: hidden !important;
  z-index: -1;
  transition: all 0.3s;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background: linear-gradient(180deg, rgba(0, 49, 55, 0.81) 0%, #003137 100%);
  position: fixed;
  top: 0px;
  z-index: 1000;
  width: 100%;
  height: 81px;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  min-width: 360px;
  max-width: 560px;
  height: 80px;
  margin: 0 auto;
  padding: 30px 16px;
}
@media (min-width: 768px) {
  .header__wrapper {
    height: 100px;
    padding: 41px 32px;
    min-width: 768px;
    max-width: 920px;
  }
}

.header__logo {
  min-width: 124px;
  min-height: 15px;
}
@media (min-width: 768px) {
  .header__logo {
    min-width: 135px;
    min-height: 17px;
  }
}

.header__toggle {
  width: 35px;
  height: 22px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__toggle {
    width: 37px;
  }
}

.lang {
  display: none;
}
@media (min-width: 768px) {
  .lang {
    display: block;
  }
}

.lang__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.lang__link {
  position: relative;
  color: #ffffff;
}

.lang__link--active::before {
  position: absolute;
  content: "";
  right: -10px;
  top: 5px;
  width: 7px;
  height: 5px;
  background-image: url("../img/header_lang_menu_icon.svg");
  background-repeat: no-repeat;
}

.navigation {
  z-index: 150;
  width: 100%;
  position: fixed;
  left: 0px;
  right: 0px;
  top: 80px;
  bottom: 0px;
  background-color: #003137;
}

.navigation__list::before {
  content: "";
  width: 100%;
  height: 100vh;
  position: absolute;
  left: max(16px, 50vw - 285px);
  top: -4px;
  z-index: -1;
  background-image: url("data:image/svg+xml, %3Csvg fill='none' stroke='%235D9F93' xmlns='http://www.w3.org/2000/svg' id='icon-plus' viewBox='0.9 0.9 24.2 24.2'%3E%3Cg stroke-width='0.07' fill-rule='evenodd'%3E%3Cg id='Learn' transform='translate(-249.000000, -3678.000000)'%3E%3Cg id='Group-6' transform='translate(250.000000, 3679.000000)'%3E%3Cpath d='M24,24 L12.0006916,24 C5.40141779,24 0,18.5999654 0,12.0006916 C0,5.40141779 5.40141779,0 12.0006916,0 C18.5999654,0 24,5.40141779 24,12.0006916 L24,24 Z' vector-effect='non-scaling-stroke' stroke-width='2px'/%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: 760px 760px;
  transform: rotate(180deg);
}

.navigation__list {
  display: flex;
  flex-direction: column;
  gap: 0px;
  z-index: 2;
  margin: 0;
  padding: 0;
  list-style-type: none;
  min-width: 360px;
  max-width: 560px;
  padding-top: 21px;
  padding-left: 65px;
  letter-spacing: -3px;
  margin: auto;
  width: 100%;
}

.navigation__item {
  padding: 10px;
}

.navigation__link {
  display: inline-block;
  min-height: 35px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 50px;
  line-height: 70px;
  color: #5D9F93;
  cursor: pointer;
}

.main__wrapper {
  margin: 0 auto;
  min-width: 360px;
  max-width: 560px;
}
@media (min-width: 768px) {
  .main__wrapper {
    min-width: 768px;
    max-width: 920px;
  }
}

.main__container {
  background-color: #003137;
  overflow: hidden;
}

.main__image {
  display: block;
  aspect-ratio: 1.49;
}

.main__banner {
  position: relative;
  z-index: 5;
}

.main__play-sing {
  position: absolute;
  top: calc(50% - 23px);
  left: calc(50% - 23px);
  cursor: pointer;
}

.main {
  padding-top: 81px;
}

.about-us {
  position: relative;
  padding: 105px 4.4% 198px;
  background-color: #003137;
}

.about-us__title {
  position: relative;
  padding: 0 40px 40px 0;
  margin: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 55px;
  line-height: 52px;
  color: #E1F1E3;
  letter-spacing: -3px;
}
@media (min-width: 768px) {
  .about-us__title {
    font-size: 75px;
    line-height: 78px;
  }
}

.about-us__content {
  position: relative;
  margin: 0;
  padding: 0 35px 0 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: #E1F1E3;
}
@media (min-width: 768px) {
  .about-us__content {
    font-size: 28px;
    line-height: 128%;
  }
}
.about-us__content::before {
  content: "";
  position: absolute;
  right: 0;
  background-image: url("data:image/svg+xml, %3Csvg fill='none' stroke='%235D9F93' xmlns='http://www.w3.org/2000/svg' id='icon-plus' viewBox='0.9 0.9 24.2 24.2'%3E%3Cg stroke-width='0.07' fill-rule='evenodd'%3E%3Cg id='Learn' transform='translate(-249.000000, -3678.000000)'%3E%3Cg id='Group-6' transform='translate(250.000000, 3679.000000)'%3E%3Cpath d='M24,24 L12.0006916,24 C5.40141779,24 0,18.5999654 0,12.0006916 C0,5.40141779 5.40141779,0 12.0006916,0 C18.5999654,0 24,5.40141779 24,12.0006916 L24,24 Z' vector-effect='non-scaling-stroke' stroke-width='2px'/%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  bottom: -40px;
  background-position: bottom right;
  width: 100vw;
  height: 700px;
  background-repeat: no-repeat;
  bottom: -40px;
  background-size: min(200vw, 900px) min(200vw, 900px);
}

.projects {
  position: relative;
  padding: 53px 4.4% 90px;
  background: #ffffff;
}

@media (min-width: 768px) {
  .projects__box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
  }
}
.projects__title {
  margin: 0;
  padding: 0 0 12px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 69px;
  color: #151919;
  letter-spacing: -2px;
}

.projects__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
@media (min-width: 768px) {
  .projects__container {
    justify-content: end;
    margin-top: -70px;
  }
}

.projects__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 50px;
  padding: 12px 36px;
  background: #E0804E;
  border-radius: 64px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
}

.projects__card:hover .card__image {
  filter: blur(17px);
}

.projects__card a {
  display: block;
  overflow: hidden;
  position: relative;
}

.projects__card .projects__button {
  background: transparent;
  position: absolute;
  border-color: white;
  top: calc(50% - 25px);
  z-index: 100;
  cursor: pointer;
  visibility: hidden;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

.projects__card:hover .projects__button {
  visibility: visible;
}

.card {
  padding-bottom: 46px;
}
@media (min-width: 768px) {
  .card {
    width: 45%;
  }
}

.card__image {
  display: block;
  width: 100%;
  margin: 0 auto;
}

.card__text {
  padding: 5px 0 0;
  margin: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  color: #151919;
  letter-spacing: -1px;
}

.work {
  z-index: 5;
  position: relative;
  background-color: #003137;
  overflow: hidden;
}

.work__container {
  padding: 23px 4.4% 54px;
}

.work__slogan {
  position: relative;
  margin: 0;
  padding: 0 0 70px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 55px;
  line-height: 58px;
  color: #E1F1E3;
  letter-spacing: -3px;
}
.work__slogan::before {
  position: absolute;
  content: "";
  bottom: 30px;
  right: 0px;
  width: min(120vw, 750px);
  height: min(120vw, 750px);
  background-image: url("data:image/svg+xml, %3Csvg fill='none' stroke='%235D9F93' xmlns='http://www.w3.org/2000/svg' id='icon-plus' viewBox='0.9 0.9 24.2 24.2'%3E%3Cg stroke-width='0.07' fill-rule='evenodd'%3E%3Cg id='Learn' transform='translate(-249.000000, -3678.000000)'%3E%3Cg id='Group-6' transform='translate(250.000000, 3679.000000)'%3E%3Cpath d='M24,24 L12.0006916,24 C5.40141779,24 0,18.5999654 0,12.0006916 C0,5.40141779 5.40141779,0 12.0006916,0 C18.5999654,0 24,5.40141779 24,12.0006916 L24,24 Z' vector-effect='non-scaling-stroke' stroke-width='2px'/%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: top left;
  background-size: cover;
}

.work__text {
  margin: 0;
  padding: 0 0 28px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #E1F1E3;
  letter-spacing: -0.5px;
}

.work__title {
  margin: 0;
  padding: 0 0 7px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 52px;
  color: #E1F1E3;
  letter-spacing: -1px;
}

.work__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.work__item {
  padding: 20px 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: #E1F1E3;
  border-bottom: 1px solid #5D9F93;
  letter-spacing: -1px;
}

.work__item:last-child {
  border-bottom: none;
}

.construction {
  background-color: #E1F1E3;
  overflow: hidden;
}

.construction__container {
  position: relative;
  padding: 12px 16px 50px;
}

.construction__slogan {
  margin: 0 0 43px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 55px;
  line-height: 58px;
  color: #003137;
  letter-spacing: -3px;
}

.construction__text {
  position: relative;
  margin: 0 0 73px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #003137;
  letter-spacing: -0.5px;
}
.construction__text::before {
  position: absolute;
  content: "";
  left: calc(50% - 700px);
  bottom: -50px;
  width: 1400px;
  height: 1400px;
  background-image: url("data:image/svg+xml, %3Csvg fill='none' stroke='%235D9F93' xmlns='http://www.w3.org/2000/svg' id='icon-plus' viewBox='0.9 0.9 24.2 24.2'%3E%3Cg stroke-width='0.07' fill-rule='evenodd'%3E%3Cg id='Learn' transform='translate(-249.000000, -3678.000000)'%3E%3Cg id='Group-6' transform='translate(250.000000, 3679.000000)'%3E%3Cpath d='M24,24 L12.0006916,24 C5.40141779,24 0,18.5999654 0,12.0006916 C0,5.40141779 5.40141779,0 12.0006916,0 C18.5999654,0 24,5.40141779 24,12.0006916 L24,24 Z' vector-effect='non-scaling-stroke' stroke-width='2px'/%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: top left;
  transform: rotate(180deg);
}

.construction__title {
  margin: 0 0 22px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 52px;
  color: #003137;
}

.construction__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.construction__item {
  padding: 17px 0;
  border-bottom: 1px solid #5D9F93;
  letter-spacing: -0.5px;
}

.construction__item:last-child {
  border-bottom: none;
}

.biome {
  background-color: #003137;
}

.biome__container--up {
  position: relative;
  min-height: 470px;
  padding: 48px 16px 290px;
  z-index: 5;
}

.biome__container--down {
  min-height: 572px;
  padding: 135px 16px 195px;
}

.biome__title {
  margin: 0;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 44px;
  color: #003137;
  z-index: 5;
  letter-spacing: -3px;
}

.biome__wrapper {
  position: relative;
  background-color: #f3f1d8;
}

.biome__wrapper::before {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  z-index: 1;
  background-image: url("../img/biom.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  filter: blur(14px);
  opacity: 0.3;
}

.biome__image {
  display: block;
  width: 328px;
  position: absolute;
  bottom: -85px;
  left: calc(50% - 164px);
  right: 0;
  z-index: 5;
}

.biome__text {
  margin: 0;
  padding: 0 0 57px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 140%;
  color: #E1F1E3;
  letter-spacing: -1px;
}

.biome__button {
  width: 200px;
  min-height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 36px;
  background: #E0804E;
  border-radius: 64px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
}

